import React, { useState } from 'react';
import PropTable from './PropTable';
import 'bootstrap/dist/css/bootstrap.min.css';

const PropComparisons = () => {
  const [selectedAssetTypes, setSelectedAssetTypes] = useState([]);
  const [selectedAccountSizes, setSelectedAccountSizes] = useState([]);
  const [applyDiscount, setApplyDiscount] = useState(false);

  const accountSizes = ['$10,000', '$25,000', '$50,000', '$75,000', '$100,000', '$200,000', '$250,000', '$300,000'];

  const toggleSelection = (item, setSelectedItems, selectedItems) => {
    const updatedSelection = selectedItems.includes(item)
      ? selectedItems.filter(i => i !== item)
      : [...selectedItems, item];
  
    setSelectedItems(updatedSelection);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Selected Asset Types:', selectedAssetTypes);
    console.log('Selected Account Sizes:', selectedAccountSizes);
    console.log('Apply Discount:', applyDiscount);
  };

  const filters = {
    selectedAssetTypes,
    selectedAccountSizes,
    applyDiscount,
  };

  return (
    <div className="prop-container">
      <form onSubmit={handleSubmit}>
        <div className="prop-section">
          <label>Select Asset Types</label>
          <div className="prop-button-group">
            <button
              type="button"
              className={`prop-btn ${selectedAssetTypes.includes('Unrealized Trailing') ? 'prop-btn-selected' : ''}`}
              onClick={() => toggleSelection('Unrealized Trailing', setSelectedAssetTypes, selectedAssetTypes)}
            >
              Unrealized Trailing
            </button>
            <button
              type="button"
              className={`prop-btn ${selectedAssetTypes.includes('Static') ? 'prop-btn-selected' : ''}`}
              onClick={() => toggleSelection('Static', setSelectedAssetTypes, selectedAssetTypes)}
            >
              Static
            </button>
            <button
              type="button"
              className={`prop-btn ${selectedAssetTypes.includes('Fast Track (14 day max)') ? 'prop-btn-selected' : ''}`}
              onClick={() => toggleSelection('Fast Track (14 day max)', setSelectedAssetTypes, selectedAssetTypes)}
            >
              Fast Track (14 day max)
            </button>
            <button
              type="button"
              className={`prop-btn ${selectedAssetTypes.includes('EOD') ? 'prop-btn-selected' : ''}`}
              onClick={() => toggleSelection('EOD', setSelectedAssetTypes, selectedAssetTypes)}
            >
              EOD
            </button>
            <button
              type="button"
              className={`prop-btn ${selectedAssetTypes.includes('Swing') ? 'prop-btn-selected' : ''}`}
              onClick={() => toggleSelection('Swing', setSelectedAssetTypes, selectedAssetTypes)}
            >
              Swing
            </button>
          </div>
        </div>
        <div className="prop-section">
          <label>Select your account size</label>
          <div className="prop-button-group">
            {accountSizes.map(size => (
              <button
                key={size}
                type="button"
                className={`prop-btn ${selectedAccountSizes.includes(size) ? 'prop-btn-selected' : ''}`}
                onClick={() => toggleSelection(size, setSelectedAccountSizes, selectedAccountSizes)}
              >
                {size}
              </button>
            ))}
          </div>
        </div>
        <div className="prop-section">
          <label>Apply Discount</label>
          <div className="prop-button-group">
            <button
                type="button"
                className={`prop-btn ${applyDiscount ? 'prop-btn-selected' : ''}`}
                onClick={() => setApplyDiscount(!applyDiscount)}
              >
                Hell Yes!
              </button>
            </div>
        </div>
      </form>
      <br />
      <PropTable filters={filters} />
    </div>
  );
};

export default PropComparisons;
